.Modal {
    position: fixed;
    z-index: 500;
    width: 80%;
    /* padding: 40px; */
    left: 10%;
    top: 5%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

@media (min-width: 800px) {
    .Modal {
        width: 800px;
        left: calc(50% - 400px);
        height: 90%;
    }
}


