.Typing {
  color: white;
  text-align: left;
}

.Typing p{
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
}
.Typing p:nth-child(1) {
  width: 100%;
  font-size: 20px;
  -webkit-animation: type 2s steps(40, end);
          animation: type 2s steps(40, end);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.Typing p:nth-child(2) {
  width: 100%;
  opacity: 0;
  -webkit-animation: type2 3s steps(80, end);
          animation: type2 3s steps(80, end);
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.Typing p:nth-child(3) {
  width: 100%;
  opacity: 0;
  -webkit-animation: type3 4s steps(80, end);
          animation: type3 4s steps(80, end);
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.Typing p:nth-child(4) {
  width: 100%;
  opacity: 0;
  -webkit-animation: type4 4s steps(80, end);
          animation: type4 4s steps(80, end);
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes type {
  0% {
    width: 0;
  }
  100% {
    width: 200px;
  }
}

@keyframes type {
  0% {
    width: 0;
  }
  100% {
    width: 200px;
  }
}

@-webkit-keyframes type2 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes type2 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes type3 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes type3 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes type4 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes type4 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}


